* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.Header-class {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  padding-top: 20px;
  padding-right: 70px;
  padding-left: 70px;
}
@media (max-width: 768px) {
  .Header-class {
    padding: 1rem;
  }
}
.Header-class .logo img {
  width: 80px;
  display: none;
}
.Header-class .menu {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media (max-width: 768px) {
  .Header-class .menu {
    display: none;
    position: absolute;
    top: 60px;
    right: 0;
    width: 100%;
    background-color: #0a192f;
    padding: 1rem;
  }
  .Header-class .menu.show {
    display: flex;
  }
}
.Header-class .menu .menu-item {
  display: flex;
  list-style: none;
  gap: 20px;
  align-items: center;
}
@media (max-width: 768px) {
  .Header-class .menu .menu-item {
    flex-direction: column;
    align-items: flex-start;
  }
}
.Header-class .menu .menu-item li {
  font-size: 1.2rem;
  font-weight: 500;
  font-family: "Moderustic", sans-serif;
  color: #ccd6f6;
  text-decoration: none;
  cursor: pointer;
}
.Header-class .menu .menu-item .reach-out-button {
  border: 2px solid #64ffda;
  padding: 10px 15px;
  border-radius: 15px;
  background-color: transparent;
  color: #64ffda;
  font-family: "Moderustic", sans-serif;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}
.Header-class .menu .menu-item .reach-out-button:hover {
  background-color: #4535C1;
  color: white;
}
.Header-class .hamburger {
  display: none;
  font-size: 1.5rem;
  color: #ccd6f6;
  cursor: pointer;
}
@media (max-width: 768px) {
  .Header-class .hamburger {
    display: block;
  }
}/*# sourceMappingURL=header.css.map */