body{
  background-color: #0a192f;
}
.home-main {
    position: relative;
    display: flex;
    padding-top: 40px;
    padding-bottom: 150px;
    align-items: center;
    justify-content: center;
    gap: 150px;
   
  
    @media (max-width: 1024px) {
      gap: 80px;
    }
  
    @media (max-width: 768px) {
      flex-direction: column;
      padding-top: 60px;
      padding-bottom: 60px;
      gap: 40px;
    }
  }

  // Add these new styles for the social icons
.social-icons-side {
  position: fixed;
  left: 40px;
  bottom: 0;
  z-index: 10;

  ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style-type: none;
    padding: 0;
    margin: 0;

    &::after {
      content: '';
      display: block;
      width: 1px;
      height: 90px;
      margin: 0 auto;
      background-color: #8892b0;
    }
  }

  li {
    margin-bottom: 20px;
  }

  a {
    color: #8892b0;
    font-size: 20px;
    transition: all 0.3s ease;

    &:hover {
      color: #64ffda;
      transform: translateY(-3px);
    }
  }
}
  
  .home-image {
    padding-top: 30px;
    img {
      max-width: 400px;
      width: 100%;
  
      @media (max-width: 1024px) {
        max-width: 300px;
      }
  
      @media (max-width: 768px) {
        max-width: 250px;
        order: -1; // Move the image above the text on mobile
      }
    }
  }
  
  .home-text {
    padding-top: 30px;
    h1 {
      font-size: 3rem;
      font-weight: 700;
      color: #ccd6f6;
      font-family: "Outfit", system-ui;
  
      @media (max-width: 1024px) {
        font-size: 2.5rem;
      }
  
      @media (max-width: 768px) {
        font-size: 2rem;
      }
    }
  
    p {
      font-size: 1.5rem;
      font-weight: 400;
      color: #8892b0;
      font-family: "Moderustic", sans-serif;
  
      @media (max-width: 1024px) {
        font-size: 1.3rem;
      }
  
      @media (max-width: 768px) {
        font-size: 1.1rem;
      }
    }
  
    .typing-animation {
      display: flex;
      align-items: center;
      gap: 5px;
  
      .typing-text {
        color: #64ffda;
        white-space: nowrap; // Add this line to prevent the text from wrapping
        overflow: hidden;
        animation: typing 4s steps(20) infinite;
      }
  
      .cursor {
        animation: blink 0.7s infinite;
      }
    }
  }
  
  @keyframes typing {
    from { width: 0; }
    to { width: 100%; }
  }
  
  @keyframes blink {
    0% {
      opacity: 0;
    }
    50% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
  
  .button-container {
    display: flex;
    gap: 20px;
    margin-top: 20px;
  
    @media (max-width: 768px) {
      flex-direction: row;
      justify-content: center;
    }
  }
  
  .resume-button,
  .about-button {
    padding: 10px 20px;
    font-size: 1rem;
    font-family: "Moderustic", sans-serif;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  
    @media (max-width: 768px) {
      width: auto;
      max-width: none;
    }
  }
  
  .resume-button {
    background-color: #0a192f;
    color: #64ffda;
    border: 2px solid #64ffda;
  
    &:hover {
      background-color: #36c2ce;
      color: #000000;
    }
  }
  
  .about-button {
    background-color: #0a192f;
    color: #64ffda;
    border: 2px solid #64ffda;
  
    &:hover {
      background-color: #478ccf;
      color: white;
    }
  }